import React, { useState, useEffect } from 'react'


// import TweetList from "../tweets/components/TweetList"
import '../styles/Home.css'
import '../profiles/styles.css'
import Feed from '../components/feed/Feed'
import ProfileComponent from '../components/ProfileComponent'
import { apiGetProfileData } from '../api/profiles'
import Navbar from "../components/navbar/Navbar"

export const UserContext = React.createContext();

function Home(props) {
    const [newTweets, setNewTweets] = useState([]);
    const [tweets, setTweets] = useState([]);
    const [tweetsDidSet, setTweetsDidSet] = useState(false);
    const [nextUrl, setNextUrl] = useState();
    const [currUser, setCurrUser] = useState();
    const [currentSection, setCurrentSection] = useState('Home');

    const { username } = props



    function apiTweetListCallback (response, status) {
        if (status === 200) {
          setTweets(response.results)
          setTweetsDidSet(true)
          setNextUrl(response.next)
        } else {
          alert("There was an error")
        }
    };

    

    const handleLoadNextResponse = (response, status) => {
        if (status === 200) {
            const newTweets = [...tweets].concat(response.results)
            setTweets(newTweets)
            setNextUrl(response.next)
          } else {
            alert("There was an error")
          }
    };

    const handleUserAuthCallback = (response, status) => {
        setCurrUser(response['username'])
    };

    useEffect(() => {
        apiGetProfileData(handleUserAuthCallback, username, 'GET')
    }, [username])
    return (
        <div className="home-container">
            <Navbar setCurrentSection={setCurrentSection} currentSection={currentSection} currentUser={username}/>
            {currentSection === 'Home' &&
                <Feed currUser={currUser} newTweets={newTweets} setNewTweets={setNewTweets} 
                    tweets={tweets} setTweets={setTweets} tweetsDidSet={tweetsDidSet} callback={apiTweetListCallback}   
                    setTweetsDidSet={setTweetsDidSet} nextUrl={nextUrl} handleLoadNextResponse={handleLoadNextResponse}/>
            }
            {currentSection === 'Profile' &&
                <ProfileComponent username={username} callback={apiTweetListCallback} className="profile-view" handleLoadNextResponse={handleLoadNextResponse}/>
            }

            {currentSection === 'Settings' &&
                <></>
            }
        </div>
    )
}

export default Home;
