import React from 'react';
import ReactDOM from 'react-dom';
import TweetDetail from './tweets/components/TweetDetail'

import Home from './pages/Home';
import TweetFeedList from './tweets/components/TweetFeedList';
import TweetList from './tweets/components/TweetList';
import ProfileComponent from './components/ProfileComponent';

const e = React.createElement
const tweetList = document.getElementById("root")
if (tweetList) {
    ReactDOM.render(e(Home, tweetList.dataset), tweetList);
}

const globalTweetList = document.getElementById("global_tweets")
if (globalTweetList) {
    ReactDOM.render(<TweetList />, globalTweetList);
}

const tweetFeed = document.getElementById("tweet_feed")
if (tweetFeed) {
    const customComponent = e(TweetFeedList, tweetFeed.dataset)
    ReactDOM.render(customComponent, tweetFeed);
}

const tweetDetailElements = document.getElementById("tweet-detail-view")

if (tweetDetailElements) {
    ReactDOM.render(e(TweetDetail, tweetDetailElements.dataset), tweetDetailElements)
}

const userProfileBadgeElements = document.querySelectorAll(".twitter-profile-badge")

userProfileBadgeElements.forEach(container => {
    ReactDOM.render(
        e(ProfileComponent, container.dataset), container);
})




