import React, {useEffect, useState} from 'react';
import './Navbar.css'
import {apiUserLookup} from '../../tweets/tweet_functions'


function Navbar(props) {
    const [currentUser, setCurrentUser] = useState();
    const {currentSection} = props

    const navbarContent = [
        "Home",
        "Profile",
        "Messages",
        "Settings"
    ]

    function NavbarListItemComponent({content, currentUser, currentSection}) {
        const isCurrentSection = currentSection === content
        console.log(currentSection);

        const handleClick = (event) => {
            event.stopPropagation();
            event.preventDefault();
            if (isCurrentSection === true) {
                return
            }
    
            if (content === 'Profile') {
                window.location.href = `/${currentUser}`
            } else if (content === 'Home') {
                window.location.href = '/'
            } else if (content === 'Settings') {
                window.location.href = '/settings'
            } else if (content === 'Messages') {
                window.location.href = '/messages'
            }
        }
    
        return (
            <>
                <li onClick={handleClick} className={`navbar__list-item ${isCurrentSection && 'bold'}`}>{content && content}</li>
            </>
        )
    }

    const handleClick = (event) => {
        var hostName = 'http://localhost:3000/'
        event.stopPropagation();
        event.preventDefault();
        let path = window.location.href
        console.log(path);
        if (path !== hostName) {
            window.location.href = '/'
        }
        
    }

    useEffect(() => {
        let mounted = true
        const handleUserAuthCallback = (response, status) => {
            if (status === 200) {
                setCurrentUser(response.username);
            }
        }

        const fetchUser = async () => {
            if (mounted) {
                apiUserLookup(handleUserAuthCallback);
            } else {
                return
            }
        }

        fetchUser();
        return () => {
            mounted = false;
        }
    }, []);

    return (
        <div className="navbar__container__outer">
            <div className="navbar__container__inner">
                <div className="navbar__header">
                    <i onClick={handleClick} className="fab fa-twitter"></i>
                </div>
                <ul className='navbar__list'>
                    {navbarContent.map((content) => <NavbarListItemComponent content={content} currentUser={currentUser} currentSection={currentSection} />)}
                </ul>
            </div>
        </div>
  );
}

export default Navbar;



