import React, {useState, useEffect} from 'react'
import {UserPicture, UserDisplay} from './components'
import {apiGetProfileData} from '../api/profiles'
import numeral from 'numeral'
import './styles.css'

export const ProfileBadgeComponent = (props) => {
    const { username, className } = props
    const [didLookup, setDidLookup] = useState(false);
    const [profile, setProfile] = useState();


    const handleBackendLookup = (response, status) => {
        setDidLookup(true);
        if (status === 200) {
            setProfile(response)
        } else if (status === 404) {
            alert('There was an error finding the requested user')
        } else {
            alert("server issue")
        }
    } 

    useEffect(() => {
            let mounted = true;
            const fetchProfile = async () => {
                if (!mounted) {return}
                if (!didLookup) {
                    apiGetProfileData(handleBackendLookup, username, "GET");
                }
            }
            fetchProfile();          
    });

    return !didLookup ? "Loading" : profile ? <ProfileBadge user={profile} setProfile={setProfile} className={className}/> 
        : <span>User not found</span>
}

function ProfileBadge(props) {
    const {user, setProfile, className} = props

    const handleFollowToggle = (event) => {
        event.preventDefault();
        event.stopPropagation();
        if (!user) {return}
        apiGetProfileData(handleFollowCallback, user.username, "POST");
    }

    const handleFollowCallback = (response, status) => {
        if (status === 200) {
            setProfile(response)
        } else if (status === 403) {
            window.location.href = '/login'
        } else {
            alert("We could not find the profile in question. Please try again later.")
        }
    }

    return user ? <div className={`profile-container ${className}`}>
            <UserPicture user={user} hideLink />
            <p>
                <UserDisplay user={user} hideLink />
            </p>
            <div className="follower-following-display">
                {numeral(user.follower_count || 0).format("0a")}{" "}{user.follower_count === 1 ? 'Follower' : 'Followers'}{" "}
                {numeral(user.following_count || 0).format("0a")}{" "}Following
            </div>
            <div className='location'>{user.location}</div>
            <div className='bio'>{user.bio}</div>
            <button onClick={handleFollowToggle} className="btn btn-primary">{user.is_following ? 'Unfollow' : 'Follow'}</button>
            
    </div> : null
}



