import React from 'react';
import TweetFeedList from '../../tweets/components/TweetFeedList';
import TweetForm from '../../tweets/components/TweetForm';
import TweetList from '../../tweets/components/TweetList';

function Feed(props) {
    const {currUser, newTweets, setNewTweets,
        tweets, setTweets, tweetsDidSet, 
        callback, setTweetsDidSet, nextUrl, 
        handleLoadNextResponse} = props  
    
    return (
        <div className="twitter_feed">
            <TweetForm
                username={currUser}
                newTweets={newTweets}
                setNewTweets={setNewTweets}
                tweets={tweets}
                setTweets={setTweets}
                tweetsDidSet={tweetsDidSet}
                callback={callback}
            />
            {!currUser ?
            <TweetList
                newTweets={newTweets}
                setNewTweets={setNewTweets}
                tweets={tweets}
                setTweets={setTweets}
                username={currUser}
                tweetsDidSet={tweetsDidSet}
                setTweetsDidSet={setTweetsDidSet}
                callback={callback}
                nextUrl={nextUrl}
                handleLoadNextResponse={handleLoadNextResponse}
            /> :
            <TweetFeedList
                newTweets={newTweets}
                setNewTweets={setNewTweets}
                tweets={tweets}
                setTweets={setTweets}
                username={currUser}
                tweetsDidSet={tweetsDidSet}
                setTweetsDidSet={setTweetsDidSet}
                callback={callback}
                nextUrl={nextUrl}
                handleLoadNextResponse={handleLoadNextResponse}
            />}
        
        </div>
);
}

export default Feed;
