import React from 'react';
import {ProfileBadgeComponent} from '../profiles'
import UserTweetList from '../tweets/components/UserTweetList'

function ProfileComponent(props) {
    const {username, className, handleLoadNextResponse, nextUrl, callback,} = props;

    
    return (
        <div>
            <ProfileBadgeComponent username={username} className={className} handleLoadNextResponse={handleLoadNextResponse}/>
            <UserTweetList user={username} handleLoadNextResponse={handleLoadNextResponse} nextUrl={nextUrl} callback={callback}/>
        </div>
    );
}

export default ProfileComponent;
